import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
//弹窗
import pop from "@/components/ming-pop.vue";
var QQMapWX = require('../../utils/qqmap-wx-jssdk.min.js');
var qqmapsdk;
export default {
	components:{
		pop
	},
	data() {
		return {
			tab:{
				name:["最新订单","待审核","已发布","已接单","待付款","待评价","已完成","已取消"],
				value:["new","0","1","2","3","4","5","9"]
			},
			tab_index:0,
			master_info: '',
			tip: '',
			sum: 0,
			list: [],
			page: 1,
			complete: true,
			//弹窗位置，默认上方
			direction: "up",
			//切换一口价或者报价下单的订单索引index
			price_index:0,
			mapCtx: '',
		}
	},
	onReady() {
		//console.log(1);
		this.mapCtx = wx.createMapContext('myMap')
	},
	onShow() {
		this.master_info = app.globalData.master_info;
		if(app.isAuth()){
			this.loadData(1);
		}
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom() {
	 	if (!this.complete) {
	 		this.page++;
	 		this.loadData(this.page);
		}
	},
	methods: {
		//拨打客户电话
		callPhone(n) {
			wx.makePhoneCall({
				phoneNumber: n
			});
		},
		//获取数据
		loadData(page) {
			var status= this.tab.value[this.tab_index];
			this.request({
				url: interfaces.orderPtList,
				data: {
					page: page,
					status: status
				},
				header: {
					token: this.master_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						
						if (page == 1) {
							this.list = [];
						}

						this.sum=res.sum;

						if (!common.isBlank(res.data)) {
				
							this.list = this.list.concat(res.data);
							this.page = page;

							if (this.list.length >= res.sum) {
								this.complete = true;
								this.tip = '数据已全部加载，暂无其他数据';
							} else {
								this.complete = false;
								this.tip = '上拉加载更多';
							}
							
						} else {
							this.complete = true;
							this.tip = '数据已全部加载，暂无其他数据';
						}
						
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		tabChange(e) {
			this.tab_index=e.detail.value;
			this.loadData(1);
		},
		//师傅确认完工
		finish(id){
			var _this = this;
			wx.showModal({
				title: '提示',
				content: '确认完工了吗？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderPtFinish,
							header: {
								token: _this.master_info.token
							},
							data: {
								id: id
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									_this.loadData(1);
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		},
		
		//弹出新增报价框
		add(index){
			this.open('center');
			this.price_index=index;
		},
		
		//表单提交事件，报价下单改为一口价下单，或者一口价下单修改价格
		formSubmit(e) {
			var _this = this;
			var data = e.detail.value;
			data.order_no=this.list[this.price_index].order_no;
			if (data.name=='') {
				wx.showToast({
					title: '请输入费用名称',
					icon: "none",
				});
				return;
			}
			if (!data.price >0) {
				wx.showToast({
					title: '请输入正确的价格',
					icon: "none",
				});
				return;
			}
			//console.log(data);
			this.request({
				url: interfaces.orderAddOffer,
				data: data,
				header: {
					token: _this.master_info.token
				},
				method: 'POST',
				success: res => {
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
						this.list[this.price_index].total_price=res.data;
						this.$refs.pop.close();
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		
		//跳转订单详情页
		goDetail(id) {
			wx.navigateTo({
				url: 'ptdetail?id=' + id,
			})
		},
		
		//弹窗组件
		open(direction) {
			this.direction = direction;
			//打开弹窗
			this.$refs.pop.show();
		},
		//打开地图导航
		goStore(index){
			var _this = this;
			// 实例化API核心类
			qqmapsdk = new QQMapWX({
				key: 'WCYBZ-EWVC4-CC3U2-DZGZI-UWGK2-E2BMM'
			});
			wx.getLocation({
				type: 'gcj02',
				success: res => {
					wx.openLocation({
						latitude: Number(_this.list[index].latitude),
						longitude: Number(_this.list[index].longitude),
						scale: 16,
						name: _this.list[index].position_address //打开后显示的地址名称
					})
				},
				fail: err => {
					//console.log(err);
					if (err.errMsg === "getLocation:fail auth deny") {
						wx.showModal({
							title: '提示',
							showCancel: false,
							confirmText: '好的',
							content: '未获取到你的地理位置，暂时无法为你提供服务。请检查是否开启定位权限。或尝试重新打开小程序。',
							success: (res) => {
								if (res.confirm) {
									wx.openSetting({
										success(settingdata) {
											//console.log(settingdata)
											if (settingdata.authSetting['scope.userLocation']) {
												wx.showToast({
													title: '授权成功',
													icon: 'none',
													mask: true,
												})
											} else {
												wx.showToast({
													title: '授权失败',
													icon: 'none',
													mask: true,
												})
											}
										}
									})
								}
							}
						})
					}
				}
			});
		},
		
	}
}
